
.tab {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  background: #222;
  z-index: 10;
}
.tab .tab-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
.tab .tab-item .tab-link {
  padding-bottom: 2px;
  color: rgba(255, 255, 255, 0.5);
}
.tab .tab-item.router-link-active .tab-link {
  color: #ffcd32;
  border-bottom: 2px solid #ffcd32;
}



